import React, { useEffect, useState } from 'react'

import parse from 'html-react-parser';

import { AiOutlineDoubleLeft } from "react-icons/ai";
import { AiOutlineDoubleRight } from "react-icons/ai";

const Calendar = () => {
    const weekStr = ['日', '月', '火', '水', '木', '金', '土'];

    const [toDay, setToDay] = useState(new Date());

    const [curDate, setCurDate] = useState({ year: toDay.getFullYear(), month: toDay.getMonth() + 1 });

    const [calData, setCalData] = useState([]);

    useEffect(() => {
        setCalData(totalCalData(curDate.year, curDate.month, currentCalData(curDate.year, curDate.month)));
    }, [curDate]);

    const totalCalData = (year, month, curCal) => {
        if (curCal[0].week != 0) {
            //先月の最終日
            const beforeD = new Date(year, month - 1, 0);
            //最終日の日
            let d = beforeD.getDate();
            //最終日の曜日
            let w = beforeD.getDay();


            while (w >= 0) {
                curCal.unshift({
                    month: 'prev',
                    day: d,
                    week: w,
                    today: beforeD.getFullYear() == toDay.getFullYear() && beforeD.getMonth() == toDay.getMonth() && d == toDay.getDate()
                })
                w--;
                d--;
            }
        }

        if (curCal[0].week != 6) {
            //来月の初日
            const afterD = new Date(year, month, 1);
            //来月の日
            let d = afterD.getDate();
            //初日の曜日
            let w = afterD.getDay();

            while (w <= 6) {
                curCal.push({
                    month: 'next',
                    day: d,
                    week: w,
                    today: afterD.getFullYear() == toDay.getFullYear() && afterD.getMonth() == toDay.getMonth() && d == toDay.getDate()
                })
                w++;
                d++;
            }
        }
        return curCal;
    }

    const currentCalData = (year, month) => {
        const first = new Date(year, month - 1, 1);
        const last = new Date(year, month, 0);

        let data = [];

        let weeknum = first.getDay();
        for (let d = first.getDate(); d <= last.getDate(); d++) {
            data.push({
                month: 'current',
                day: d,
                week: weeknum,
                today: toDay.getFullYear() == year && toDay.getMonth() + 1 == month && toDay.getDate() == d
            });
            weeknum++;
            if (weeknum >= 7) {
                weeknum = 0;
            }
        }

        return data;
    }

    const createCalHtml = () => {
        let html = '<tbody>\n';

        // html += '<tr><td className=\'sun notCurrentMonth\'>1</td></tr>'


        calData.forEach((data, index) => {
            const isSun = data.week == 0;
            const isSat = data.week == 6;
            const isNotCurrentMonth = data.month != 'current';

            let cName = '';

            if (isSun) {
                cName += 'sun';
            }

            if (isSat) {
                cName += 'sat';
            }

            if (isNotCurrentMonth) {
                cName += ' notCurrentMonth'
            }

            <td><span className='today
            '>20</span></td>

            // let d = `<td className=\'${cName}\'>${data.day}</td>`;
            let d = '';
            d += `<td className=\'${cName}\'>`;
            if (data.today && !isNotCurrentMonth) {
                d += `<span className=today>${data.day}</span>`;
            } else {
                d += data.day;
            }

            d += '</td>';

            if (isSun) {
                d = `<tr>` + d;
            }

            if (isSat) {
                d = d + '</tr>';
            }

            html += d;
        })

        html += '</tbody >';

        return parse(html);
    }

    const prevCal = () => {
        let y = curDate.year;
        let m = curDate.month;
        m = m - 1;
        if (m <= 0) {
            m = 12;
            y = y - 1;
        }
        setCurDate({ year: y, month: m });
    }

    const nextCal = () => {
        let y = curDate.year;
        let m = curDate.month;
        m = m + 1;
        if (m > 12) {
            m = 1;
            y = y + 1;
        }
        setCurDate({ year: y, month: m });
    }


    return (
        <div className='calendar'>
            <div className="title">
                <h1>カレンダー</h1>
            </div>
            <div className="yearAndMonth">
                <h2>{curDate.year}年{curDate.month}月</h2>
            </div>
            <div className="btns">
                <button onClick={prevCal}><AiOutlineDoubleLeft /></button>
                <button onClick={nextCal}><AiOutlineDoubleRight /></button>
            </div>


            <div className="cal">
                <table>
                    <thead>
                        {/* 曜日 */}
                        <tr>
                            {weekStr.map((week, index) => (
                                <th key={index} className={week == '日' ? 'sun' : week == '土' ? 'sat' : ''}>
                                    <span>{week}</span>
                                </th>
                            ))}
                        </tr>
                    </thead>

                    {createCalHtml()}

                </table>
            </div>
        </div >
    )
}

export default Calendar
